import createMiddleware from "next-intl/middleware";
import { locales, defaultLocale } from "./i18n";

export default createMiddleware({
  locales,
  defaultLocale,
  localePrefix: "as-needed", // یا 'always' بسته به نیاز
});

export const config = {
  matcher: ["/((?!_next|.*\\..*).*)"], // همه مسیرها به جز فایل‌های استاتیک
};
